.StagesPager {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
  margin: 1rem;
}

.TabsPaginator {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  gap: 1rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .TabsPaginator {
    grid-template-columns: repeat(6, 1fr);
  }
}
.TabsPaginator button {
  background-color: transparent;
  border: #d9d9d9 solid 2px;
  border-radius: 100%;
  width: 3rem;
  height: 3rem;
  margin-bottom: 2rem;
  position: relative;
  cursor: pointer;
  transition: 1s all;
}
@media (min-width: 768px) {
  .TabsPaginator button {
    margin-bottom: 2rem;
  }
}
.TabsPaginator button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2rem;
  height: 2rem;
  background-color: #d9d9d9;
  border-radius: 100%;
  transition: background-color 1s;
}
.TabsPaginator button:hover {
  border-color: silver;
}
.TabsPaginator button:hover::before {
  background-color: silver;
}
.TabsPaginator button.CurrentStage {
  border: #FF7F50 solid 2px;
}
.TabsPaginator button.CurrentStage::before {
  background-color: #FF7F50; /* Color del punto interno */
}

.SectionPage {
  display: none;
}

.ShowPage {
  display: block;
  width: 100%;
}

.ContainerStage {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  background-color: #00131d;
  border-top-left-radius: 4vh;
  border-bottom-right-radius: 4vh;
}
@media (min-width: 768px) {
  .ContainerStage {
    grid-template-columns: repeat(4, 1fr);
  }
}
.ContainerStage h2 {
  grid-column: 2 span;
  text-align: center;
  margin: 0 0 2rem 0;
  color: #FFFFFF;
  font-size: 4rem;
  font-family: "bytebounce", Arial, Helvetica, sans-serif;
  font-weight: bold;
}
@media (min-width: 768px) {
  .ContainerStage h2 {
    font-size: 7rem;
    grid-column: 4 span;
  }
}
.ContainerStage h3 {
  color: white;
  font-size: 1rem;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}
.ContainerStage .SVG {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.ContainerStage .SVG svg {
  width: 75px;
  height: 75px;
  margin: 0 auto;
  cursor: pointer;
  transition: 1s transform; /* Aplica la transición a 'fill' y 'stroke' */
}
.ContainerStage .SVG svg:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .ContainerStage .SVG svg {
    width: 150px;
    height: 150px;
  }
}

footer {
  background-color: #000000;
  width: 100%;
  height: 15rem;
}
footer .FooterIcons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 1rem;
  padding: 0.5rem;
}
footer .FooterIcons .GitHub svg {
  width: 50px;
  height: 50px;
  fill: transparent; /* Color inicial del relleno */
  stroke: #fff; /* Color inicial de la línea */
  cursor: pointer;
  transition: fill 1s ease, stroke 1s ease; /* Aplica la transición a 'fill' y 'stroke' */
}
footer .FooterIcons .GitHub svg:hover {
  fill: #fff; /* Color de relleno al pasar el mouse */
  stroke: #fff; /* Color de la línea al pasar el mouse */
}
footer .FooterIcons .Linkedin svg {
  width: 50px;
  height: 50px;
  fill: #fff;
  cursor: pointer;
  transition: fill 1s ease, stroke 1s ease; /* Aplica la transición a 'fill' y 'stroke' */
}
footer .FooterIcons .Linkedin svg:hover {
  fill: #0077b5; /* Color de relleno al pasar el mouse */
}
footer .FooterIcons .GmailFooter {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
footer .FooterIcons .GmailFooter svg {
  width: 50px;
  height: 50px;
  fill: #fff;
  cursor: pointer;
}
footer .FooterIcons .GmailFooter h2 {
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  margin: 1rem 1rem;
}